    height: 42px; }
    @media (max-width: 479px) {
      .logo__wrap img {
        width: 80px;
        height: 30px; } }

.menu {
  display: flex;
  align-items: center;
  justify-content: space-between; }
  @media (max-width: 1024px) {
    .menu {
      position: relative;
      z-index: -1;
      position: fixed;
      width: 100%;
      height: 100%;
      overflow: auto;
