      display: flex;
      justify-content: space-evenly; } }
  @media (max-width: 1024px) {
    .menu__list {
      display: flex;
      flex-direction: column;
      align-items: center; } }

.menu__item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  /* Только если меньше контейнера */
  margin-right: 40px;
  padding: 5px 0;
  position: relative; }
  @media (max-width: 1300px) {
    .menu__item {
      margin-right: calc(20px + 20 * ((100vw - 320px) / 980)); } }
  .menu__item:last-child {
    margin-right: 0;
    margin-left: 40px; }
    .menu__item:last-child::before {
      content: '';
      position: absolute;
      left: -40px;
      top: 0;
      width: 1px;
      height: 100%;
      background-color: #000; }
  @media (max-width: 1024px) {
    .menu__item {
      padding: 0;
      margin: 0;
      margin-bottom: 30px; }
      .menu__item:last-child {
        margin: 0;
        margin-bottom: 100px; }
        .menu__item:last-child::before {
          left: 50%;
