          width: 150%;
          height: 1px;
          background-color: #fff; } }
  @media (max-width: 768px) {
    .menu__item {
      margin-bottom: 25px; }
      .menu__item:last-child {
        margin: 0;
        margin-bottom: 26px; }
        .menu__item:last-child::before {
